    position: relative;
    font-size: 20px;
    line-height: inherit;
    font-style: italic;
    font-weight: normal;
    padding: 10px 0 25px
}
.lead:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    height: 2px;
    width: 80px;
    background-color: rgba(0, 0, 0, 0.1)
}
.lead.text-center:after {
    left: 50%;
    margin-left: -40px
}
.section-dark .lead:after,
.section-accent .lead:after {
    background-color: rgba(255, 255, 255, 0.2)
}
.icon-list {
