}
.icon-list-icon {
    position: absolute;
    top: 0;
    left: 0;
    margin: 20px 0 0 0;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    font-size: 32px;
    line-height: 70px;
    vertical-align: middle;
    text-align: center;
    color: #fff
}
.icon-list-icon:not(.fa) {
