}
.nice-list {
    list-style: none;
    padding: 0
}
.nice-list li {
    position: relative;
    padding: 8px 0 8px 40px
}
.nice-list li:before {
    content: "\f105";
    position: absolute;
    top: 0;
    left: 0;
    margin: 10px 0 0 10px;
    font-family: "FontAwesome";
    font-size: 20px;
    line-height: 1;
    opacity: .5
}
.clear {
    clear: both
}
.wait-animation {
