.section-slider .section-slide {
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 100%
}
.section-slider ~ .rslides_nav {
    font-size: 40px;
    line-height: 50px;
    width: 50px;
    height: 50px;
    margin-top: -25px;
    background-color: #fff
}
.section-dark .section-slider ~ .rslides_nav {
    background-color: #000
}
.section-slider ~ .rslides_nav:before {
    width: 50px;
    vertical-align: top
}
.section-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    overflow: hidden
}
.section-background-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
}
.section-background-image img {
    position: absolute;
    min-width: 100%;
    max-width: none;
    min-height: 100%;
    top: 0;
    left: 0
}
.section-background-video {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%
}
.section-background-video>video {
    width: 100%;
    height: 100%
}
.section-background-slideshow {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
    opacity: 0
}
.section-background-slideshow.rslides>*:not(:first-child) {
    opacity: 0
}
.section-background-slideshow.rslides {
    opacity: 1
}
.section-background-slideshow>li {
    position: absolute;
    width: 100%;
    height: 100%
}
.section-background-slideshow img {
    position: absolute;
    min-width: 100%;
    max-width: none;
    min-height: 100%;
    top: 0;
    left: 0
}
.section-background-maps {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%
}
.section-background-dot-overlay:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
